import React, { Component } from 'react';
import { Row, Radio, Pagination } from 'antd';
import './a.css';
import ThemeCard from './single';
import { ChangeType } from '../../redux/actions/actionTypes';
import { ThemeState } from '../../redux/reducers/themeReducer';

export interface TypePageEvents {
  onLoad: () => void;
  onSwitchChange: (type: ChangeType, newState: any, id: string) => void;
  onKeyChange: (key: string) => void;
  onSearch: () => void;
  onChange: (newCondition: any, type: any) => void;
}

export default class Test extends Component<TypePageEvents & ThemeState> {
  componentDidMount() {
    if (this.props.onLoad) {
      this.props.onLoad();
    }
  }

  handleChange(page: number) {
    this.props.onChange(page, 'page');
  }

  render() {
    let arr1;
    if (this.props.data) {
      arr1 = this.props.data.map((item, id) => {
        return (
          <ThemeCard
            key={id}
            id={id}
            data={item}
            onSwitchChange={this.props.onSwitchChange}
          />
        );
      });
    }

    return (
      <div>
        <div>
          <div>
            课程分类：
            <Radio.Group
              defaultValue="全部"
              buttonStyle="solid"
              name="type"
              onChange={(e) => {
                this.props.onChange(e.target.value, e.target.name);
              }}
            >
              <Radio.Button value="全部">全部</Radio.Button>
              <Radio.Button value="技术" name="type">
                技术专区
              </Radio.Button>
              <Radio.Button value="产品" name="type">
                产品专区
              </Radio.Button>
              <Radio.Button value="商务" name="type">
                商务专区
              </Radio.Button>
              <Radio.Button value="市场" name="type">
                市场专区
              </Radio.Button>
              <Radio.Button value="内部" name="type">
                内部专区
              </Radio.Button>
            </Radio.Group>
          </div>
          <div>
            特色课程：
            <Radio.Group
              defaultValue="范式讲堂"
              buttonStyle="solid"
              name="specialContent"
              onChange={(e) => {
                this.props.onChange(e.target.value, e.target.name);
              }}
            >
              <Radio.Button value="true" name="specialContent">
                范式讲堂
              </Radio.Button>
            </Radio.Group>
          </div>
          <div>
            难易程度：
            <Radio.Group
              defaultValue="全部"
              buttonStyle="solid"
              name="difficulty"
              onChange={(e) => {
                this.props.onChange(e.target.value, e.target.name);
              }}
            >
              <Radio.Button value="" name="difficulty">
                全部
              </Radio.Button>
              <Radio.Button value="初级" name="difficulty">
                初级
              </Radio.Button>
              <Radio.Button value="中级" name="difficulty">
                中级
              </Radio.Button>
              <Radio.Button value="进阶" name="difficulty">
                进阶
              </Radio.Button>
            </Radio.Group>
          </div>
          <div>
            展示方式：
            <Radio.Group defaultValue="默认" buttonStyle="solid">
              <Radio.Button value="默认">默认</Radio.Button>
              <Radio.Button value="最新">最新</Radio.Button>
              <Radio.Button value="最热">最热</Radio.Button>
            </Radio.Group>
          </div>
        </div>
        <Row gutter={16}>{arr1}</Row>
        <Pagination
          current={this.props.condition.page}
          pageSize={this.props.condition.limit}
          total={this.props.total}
          onChange={this.handleChange.bind(this)}
        />
      </div>
    );
  }
}
